.Header {
	float: left;
	width: 100%;
	height: 90%;
	background-size: auto 124%;
	background-position: 850px -50px;
	background-repeat: no-repeat;

	&_listContainer {
		lost-column: 2/12;
		position: relative;
		top: 290px;
	}

	&_logoContainer {
		position: relative;
		top: 75px;
		lost-column: 2/12;
		lost-offset: 1/12;

		img { 
			width: 120%;
		}
	}

	&_bottleContainer {
		display: none;
		position: relative;
		top: 295px;
    	float: left;
    	width: 115px;
		lost-offset: .5/12;

		& img {
			width: 100%;
		}
	}

	&_h1 {
		font-size: 36px;
		text-transform: uppercase;
	}

	&_listItem {
		margin: 0 0 2px;
	    font-size: 1.1875rem;
	    text-transform: uppercase;
	    letter-spacing: 1.2px;
	}
}

.Delivery {
	lost-column: 2.7/12;
	position: relative;
	top: 100px;
	text-align: center;
	padding: 70px;

	&_yellow {
		text-transform: uppercase;
		color: #ebbe23;
		font-weight: 600;
		font-size: 26px;
	}
}

@media screen and (max-width: 768px) {

	.Delivery {
		lost-column: 12/12;
		position: relative;
		top: 10px;
		text-align: center;
		margin: 30px 0 95px;
		padding: 0;

		&_yellow {
			text-transform: uppercase;
			color: #ebbe23;
			font-weight: 600;
			font-size: 26px;
		}
	}

	.Header {
		background-image: none !important;
		
		&_listContainer {
			lost-column: 8/12;
			lost-offset: 0;
			top: 3px;
		}

		&_h1 {
			font-size: 2.4375rem	;
		}

		&_listItem {
			font-size: 16px;
		}

		&_logoContainer {
			lost-column: 4/12;
			lost-offset: 0;
			top: 0;
			img {
				width: 100%;
			}
		}

		&_bottleContainer {
			top: 0;
			lost-column: 3.5/12;
			lost-offset: 4.3/12;

			& img {
				width: 100%;
			}
		}
	}
}